const sWidth: number = window.innerWidth;

class SocialScreen extends Component {
    state = {
        loaded: false
    }
    seo: SeoSettingsType = {
        title: "İletişim | Can Gökçeaslan",
        description: "Bu sayfada bana ait tüm profil linklerine ulaşabilirsiniz.",
        keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        siteName: "https://www.cangokceaslan.com/social"
    }
    constructor(props) {
        super(props);
    }
    renderSocialItem(config = {
        className: "facebook",
        logoText: "Facebook",
        icon: "assets/svg/social/facebook.svg",
        link: "https://www.facebook.com/cangokceaslan",
        name: "Can Gökçeaslan",
        description: "Bu sayfayı ziyaret ederek Facebook sayfama ulaşabilirsin"
    }) {
        return (
            <div className="col-lg-4 col-md-6 col-12 item" onClick={() => {
                window.open(config?.link, "__blank")
            }}>
                <div className="social-item">
                    <div className={`logo-container ${config?.className}`}>
                        <div style={{ display: "flex" }}>
                            <img src={config?.icon} style={{ marginRight: "6px" }} width="30px" height="30px" />
                            <h3>{config?.logoText}</h3>
                        </div>
                        <div className="profilePhotoContainer">
                            <img src="assets/jpg/social.jpg" width="100px" height="100px" alt="Can Gökçeaslan'ın ana resimi" />
                        </div>
                    </div>
                    <div className="name">
                        {config?.name}
                    </div>
                    <div className="description">
                        {config?.description}
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount() {
        this.setState({
            loaded: true
        })
    }
    render() {
        if (!this.state.loaded) return <div className="loading">Yükleniyor</div>
        return (
            <React.Fragment>
                <DocumentSettings docData={this.seo} />
                <h1 style={{ display: "none" }}>İletişim</h1>
                <div className="social" style={{
                    paddingTop: "40px",
                    marginBottom: "20px",
                    ...(sWidth < 992) && {
                        marginTop: "-40px"
                    }
                }} >
                    <div className="container">
                        <div className="row">
                            {data.map((config, i) => this.renderSocialItem(config))}
                        </div>
                    </div>
                </div>
                <NewCourse />
            </React.Fragment>
        )
    }
}


